.popup__container {
    width: 430px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    position: relative;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 34px 36px 37px;
    box-sizing: border-box;
    
  }
  
  @media screen and (max-width: 767px) {
    .popup__container {
      width: 282px;
      padding: 25px 22px;
      
    }
  }
  

  