.profile {
    max-width: 880px;
    margin: 40px auto 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  @media screen and (max-width: 920px) {
    .profile {
      margin-top: 42px;
      max-width: 600px;
      flex-direction: column;
    }
  }
  
  @media screen and (max-width: 767px) {
    .profile {
      max-width: 282px;
    }
  }
  