.profile__job {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    grid-area: profile__job;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    
}

@media screen and (max-width: 767px) {
    .profile__job {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
    }
}
  